export default{
	data(){
		return{   
			//xianshi:false,
			  title: 'picker',
			index: 1,
			    arryObject: [{
			                              id: 1,
			                              name: '行业新闻' ,
										  
			                          },
			                          {
			                             id: 2,
			                              name: '行业消息',
			                             
			                          },
			                          {
			                             id: 3,
			                              name: '人事需求'
			                          },
			                          {
			                            id: 4,
			                              name: '食材需求'
			                          },
			                          {
			                             id: 5,
			                              name: '餐饮器件需求'
			                          },
			                       
			                      ],
			 
			imgList: [],
			modalName: null,
			textareaAValue: '',
			textareaBValue: '',
			form:{
				name:"",
				miaoshu:"",
				xuanze:"",
				content:""
			},
		
		}
	},
	onLoad() {
		/* this.quanxian(); */
	},
	methods:{
		
	
		
		DelImg(e) {
			uni.showModal({
				title: '小姐姐小哥哥',
				content: '确定要删除这张图片嘛？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		
		
		
		  bindPickerChange: function(e) {
			  this.index = e.target.value
			  this.form.xuanze=this.arryObject[e.detail.value].id;
		},
		tijioa(){
			let that =this;
			
			console.log("2")
		if(that.form.name == "" ||that.form.content == "" || that.form.miaoshu == "" || that.form.xuanze == "" || that.imgList==[]){
			console.log("1")
			uni.showToast({
			    title: '请填写必要信息',
			    duration: 2000,
				icon:"none"
			});
		}	
		else
		{
			 const token = uni.getStorageSync('token');
			 uni.uploadFile({
			     url : 'http://localhost:58793/Api/Home/content',
			     filePath: this.imgList[0],
				
				 fileType:'image',
			     name: 'file',
				   header:{
					   
					   "Content-Type": "multipart/form-data",
				   "token":token
				   },

			     formData: {
			     News_Title:that.form.name,
			 	News_Content:that.form.content,
			     News_Describe:that.form.miaoshu,
			     News_Type_ID:that.form.xuanze
			     },
			     success: function (uploadFileRes) {
			    
				  that.form.name="",
				  that.form.content="",
				  that.form.miaoshu="",
				  
				 that.imgList=[]
				 }
			    });
	}
		 },
		//上传图片
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths);
						 // const uploadTask = uni.uploadFile({
						 // url : '',
						 //      filePath: res.tempFilePaths[0],
						 //      name: 'file',
						 //      formData: {
						 //       'user': 'test'
						 //      },
						 //      success: function (uploadFileRes) {
						 //       console.log(uploadFileRes.data);
						 //      }
						 //     });
					} else {
						this.imgList = res.tempFilePaths
					};
					console.log(this.imgList);
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
	}
}
